var indexFontSize = function(index) {
  if (index === 0) {
    return '14pt';
  }
  if (index === 1) {
    return '13pt';
  }
  if (index === 2) {
    return '13pt';
  }
  return '11pt';
};

var indexColor = function(index) {
  if (index === 0) {
    return '#9F7E2A';
  }
  if (index === 1) {
    return '#4a4F6B';
  }
  if (index === 2) {
    return '#442A07';
  }
  if (index % 2 !== 0) {
    return '#000000';
  }
  return '#332222';
};

var namedColor = function(name) {
  if (name.includes('JustMan')) {
    return 'orange';
  }
  if (name.includes('Hurumbum')) {
    return '#FC4676';
  }
  return '#E8E3C5';
};

var scoredColor = function(score) {
  if (score === '58') {
    return '#FF4A3A';
  }
  if (score === '43') {
    return '#26BBFF';
  }
  if (score === '40') {
    return '#A0FF76';
  }
  if (score < '40' && score > '0') {
    return '#DAFDEB';
  }
  return 'orange';
};

var scoredGPColor = function(score) {
  if (score > '90') {
    return '#FF4A5A';
  }
  if (score > '65') {
    return '#26BBFF';
  }
  if (score > '46') {
    return '#A0FF76';
  }
  if (score < '47' && score > '0') {
    return '#DAFDEB';
  }
  return 'orange';
};

var totalScoreColor = function() {
  return '#8899CD';
};

var getScoreColor = function(data, column) {
  if (column === 0) {
    return namedColor(data[column]);
  }
  if (column === 1 || column === 2) {
    return scoredColor(data[column]);
  }
  if (column === 3) {
    return scoredGPColor(data[column]);
  }
  if (column === 4) {
    return totalScoreColor();
  }
  return 'white';
};

jQuery(document).ready(function($) {
  $('#rating_table').dataTable({
    data: dataSet,
    columns: [
      {title: 'Name'},
      {title: 'Last Qlf'},
      {title: 'Last Race'},
      {title: 'Last GP'},
      {title: 'Total Points'},
      {title: 'reserved'}
    ],
    order: [[4, 'desc']],
    createdRow: function(row, data) {
      $(row).find('td').each(function(column, td) {
        $(td).css({
          color: getScoreColor(data, column)
        });
      });
    },
    fnRowCallback: function(nRow, aData, iDisplayIndex, iDisplayIndexFull) {
      $(nRow).children().css({
        background: indexColor(iDisplayIndexFull)
      }).css('font-size', indexFontSize(iDisplayIndexFull));
    }
  });
});
